<!-- 正则模块 -->
<script>
  //用相对路径！！
  import Field from "../ui/Field.svelte";
  import CheckBox from "../ui/CheckBox.svelte";
  import * as Icon from "../ui/icon";
  export let input = "";
  export let output = "";
  let reg = "";
  let toreplace = "";
  let searchResult = "";
  let globalSearch = false;
  let capsSearch = false;
  let multilineSearch = false;

  function convert() {
    let param = "";
    if (globalSearch) {
      param += "g";
    }
    if (multilineSearch) {
      param += "m";
    }
    if (capsSearch) {
      param += "i";
    }

    let regex;
    try {
      regex = RegExp(reg, param);
    } catch (e) {
      searchResult = e;
      return;
    }

    output = input.replace(regex, toreplace);
    searchResult = input.match(regex);
    if (searchResult != null) {
      searchResult = searchResult.reduce((pre, cur, index) => `${pre}\n${cur}`);
    }
  }
</script>

<div class="columns">
  <div class="column">
    <Field label="正则表达式">
      <input
        class="input"
        type="text"
        placeholder="正则表达式"
        bind:value={reg} />
    </Field>
  </div>
  <div class="column">
    <Field label="选项">
      <CheckBox label="全局搜索" bind:checked={globalSearch} />
      <CheckBox label="不区分大小写" bind:checked={capsSearch} />
      <CheckBox label="多行搜索" bind:checked={multilineSearch} />
    </Field>
  </div>
</div>

<div class="columns">
  <div class="column">
    <Field label="替换字符串">
      <textarea
        class="textarea"
        placeholder="替换字符串"
        bind:value={toreplace} />
    </Field>
  </div>
  <div class="column">
    <Field
      label="查找结果 {searchResult.length <= 0 ? '' : `（${searchResult.length}）`}">
      <textarea
        class="textarea"
        placeholder="查找结果"
        bind:value={searchResult} />
    </Field>
  </div>
</div>
<Field>
  <button class="button is-primary " on:click={convert}>
    {@html Icon.rotate}
    转换
  </button>
</Field>
